@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,400i,600,600i,700,700i,900,900i);
body {
  background: #f3f3f3;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 700; }
  body * {
    margin: 0;
    padding: 0;
    font-style: normal; }

a {
  text-decoration: none;
  color: #2E59BD;
  text-decoration: underline; }

h1 {
  font-size: 330%;
  margin: 0;
  color: #2E59BD; }

p {
  font-weight: 300;
  color: #f3f3f3; }

.container {
  background-color: #ffffff;
  max-width: 600px;
  margin: 20px auto;
  padding: 40px 50px 60px 50px;
  text-align: center;
  border-radius: 20px; }

span {
  display: block; }

.count {
  padding: 30px;
  font-size: 100px; }

button {
  padding: 20px 40px;
  font-size: 30px;
  border: 0;
  background-color: #f1f1f1;
  border-radius: 15px; }

button:hover {
  cursor: pointer; }

button:focus {
  outline: none; }

.plus {
  margin-right: 20px; }

.reset {
  margin-top: 20px;
  padding: 20px 70px; }

small {
  font-size: 100%;
  display: block;
  color: #2E59BD;
  font-weight: normal;
  margin-top: 50px; }

@media screen and (max-width: 600px) {
  h1 {
    font-size: 280%; } }

